import { all, create } from "mathjs";

const fns = [
    'asin', 'acos', 'atan', 'acot', 'acsc', 'asec',
    'sin', 'cos', 'tan', 'sec', 'cot', 'csc',
    'exp', 'log', 'log10', 'log1p', 'log2',
    'sign', 'floor', 'ceil', 'sqrt'
];

/**
 * 
 * @param {String} str 
 * @param {String[]} anyOf 
 * @param {Number} startIndex 
 * @returns {Number}
 */
const indexOfAny = (str, anyOf, startIndex = 0) => {
    for (let i = startIndex; i < str.length; i++) {
        if (anyOf.includes(str.charAt(i))) {
            return i;
        }
    }

    return -1;
};

/**
 * 
 * @param {String} expr 
 * @returns {String}
 */
const fixExpr = (expr) => {
    if (expr.length > 0) {
        fns.forEach(fn => {
            fn += ' ';
            let index = expr.indexOf(fn);

            while (index > -1 && index <= expr.length) {
                index += fn.length;
                expr = expr.slice(0, index) + '(' + expr.slice(index);

                index = indexOfAny(expr, [' ', ')'], index);

                if (index < 0) {
                    index = expr.length;
                }

                expr = expr.slice(0, index) + ')' + expr.slice(index);
                index = expr.indexOf(fn, index);
            }
        });
    }

    return expr;
};

const math = create(all, {
    number: 'BigNumber',
    precision: 32,
    parenthesis: 'auto',
});

/**
 * 
 * @param {String} expr 
 * @param {Object} scope 
 * @returns {String}
 */
export const evaluate = (expr, scope) => {
    expr = fixExpr(expr);
    let ans = '';
    for (let i = 0; i < expr.length; i++) {
        if (expr[i] === '/') {
            i++;
            expr = expr.slice(0, i) + '(' + expr.slice(i);

            let index = expr.indexOf(')', i);
            if (index < 0) {
                index = expr.length;
            }

            expr = expr.slice(0, index) + ')' + expr.slice(index);
        }
    }

    expr = expr.split(':').join('/');

    try {
        ans = math.evaluate(expr, scope);

        if (isNaN(ans)) {
            ans = 'NaN';
        }
    } catch {
        ans = 'Error';
    }

    return ans;
};

let replacements = {};

const fns1 = ['sin', 'cos', 'tan', 'sec', 'cot', 'csc'];
fns1.forEach(function (name) {
    const fn = math[name]

    const fnBigNumber = function (x) {
        return fn(math.multiply(x, math.divide(math.pi, math.bignumber(180))));
    };

    // create a typed-function which check the input types
    replacements[name] = math.typed(name, {
        'BigNumber': fnBigNumber,
        'Array | Matrix': function (x) {
            return math.map(x, fnBigNumber)
        }
    });
});

const fns2 = ['asin', 'acos', 'atan', 'atan2', 'acot', 'acsc', 'asec'];
fns2.forEach(function (name) {
    const fn = math[name]

    const fnBigNumber = function (x) {
        const result = fn(x)
        return math.multiply(result, math.divide(math.bignumber(180), math.pi));
    };

    replacements[name] = math.typed(name, {
        'BigNumber': fnBigNumber,
        'Array | Matrix': function (x) {
            return math.map(x, fnBigNumber)
        }
    })
});

math.import(replacements, { override: true });

export default math;